import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { of } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';

import { HandleErrorService } from './handle-error.service';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class TestimonialVideoService {
  testimonialVideoUrl = environment.contentServer + '/api/contentMgmt/getTestimonialUrl';
  videoUrl: any;

  constructor(private httpClient: HttpClient, private handleErrorService: HandleErrorService) { }

  getTestimonialVideoUrl() {
    if (this.videoUrl && this.videoUrl !== '') {
      return of(this.videoUrl);
    } else {
      return this.httpClient.get<any>(this.testimonialVideoUrl, { observe: 'response' })
        .pipe(
          map(res => {
            if (res['status'] === 200 && res['body'] && res['body'].testimonialUrl) {
              this.videoUrl = res['body'].testimonialUrl;
            } else {
              this.videoUrl = '';
            }
            return this.videoUrl;
          }),
          catchError(this.handleErrorService.handleError('getTestimonialUrl', []))
        );
    }
  }
}
